import { Capacitor } from '@capacitor/core'
import { File } from '@ionic-native/file'
import { IonAlert } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { AdminItemDetail, Page } from '../../components'
import { RUTAS_WEB_TRANSPORTISTA } from '../../constants'
import { nroTelefonoLocal, openPageNow } from '../../helpers'
import {cambiarModo, useApiToaster, useToaster} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'

const FileDownload = require('js-file-download')

const moment = require('moment-timezone')

export const AdminTransportistaPerfil: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [Transportista, setTransportista] = useState(null as any)
  const [AlertContrato, setAlertContrato] = useState(false)
  const toaster = useToaster()
  const apiToaster = useApiToaster()

  const setup = () => {
    if (loginStore.transportista) {
      obtenerTransportista(loginStore.transportista.id)
    }
  }

  React.useEffect(() => {
    setup()
  }, [])

  const obtenerTransportista = (idTransportista: number) => {
    rootStore.environment.api.adminGetTransportista(idTransportista).then((result) => {
      if (result.kind === 'unauthorized') {
        rootStore.loginStore.reset()
        rootStore.loginStore.setApiToken(null)
        document.location.reload()
      }
      if (result.kind === 'ok') {
        setTransportista(result.transportista)
        //
        if (result.transportista.bloqueado) {
          alert("Su perfil de driver se encuentra deshabilitado, por favor contacte con la administración")
          // Cambiar a modo cliente
          cambiarModo(rootStore, history, 10)
        }
      }
    })
  }

  const obtenerContrato = (tipo: string) => {
    if (!Transportista.direccion) {
      toaster(`Para descargar su contrato primero cargue su dirección`)
      openPageNow(RUTAS_WEB_TRANSPORTISTA.EDITAR, history)
      return
    }
    rootStore.environment.api.getContrato(tipo).then((result) => {
      if (result.kind === 'ok') {
        if (Capacitor.getPlatform() === 'web') {
          FileDownload(result.data, 'contrato-persona-' + tipo + '.pdf')
        } else {
          const dwpath =
            Capacitor.getPlatform() === 'ios' ? `${File.documentsDirectory}` : `${File.externalRootDirectory}Download/`
          const dwpathReadable =
            Capacitor.getPlatform() === 'ios'
              ? `Documents/contrato-persona-${tipo}.pdf`
              : `Download/contrato-persona-${tipo}.pdf`

          const blobi = new Blob([result.data], { type: 'application/pdf' })
          File.writeFile(dwpath, `contrato-${tipo}.pdf`, blobi, { replace: true })
            .then((res) => {
              toaster(`Contrato descargado exitosamente, guardado en ${dwpathReadable}`)
            })
            .catch((error) => {
              toaster(`Error al descargar el contrato, vuelva a intentar en unos momentos`, 'error')
            })
        }
      } else {
        apiToaster(result, '', 'descargar contrato')
      }
    })
  }

  const aceptarContrato = () => {
    rootStore.environment.api.aceptarContrato('transportista').then((result) => {
      if (result.kind === 'ok') {
        setTransportista(result.response)
      }
    })
  }

  return (
    <Page login_required admin>
      <IonAlert
        isOpen={AlertContrato}
        onDidDismiss={() => setAlertContrato(false)}
        header={'Aceptar contrato'}
        message={'Confirma que desea aceptar el contrato con ABi'}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Confirmar',
            handler: () => aceptarContrato(),
          },
        ]}
      />
      {Transportista && (
        <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
          <div className="container-fluid background-white">
            <div className="row page-titles">
              <div className="col-md-5 col-12 align-self-center">
                <h3 className="text-themecolor">Mi perfil</h3>
              </div>
              <div className="col-md-7 col-12 align-self-center d-none d-md-block">
                <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                  <li className="breadcrumb-item active">Inicio</li>
                </ol>
              </div>
            </div>
            <div className={'mb-2 text-right'}>
              <button
                className={'btn btn-primary ml-3'}
                title={'Editar'}
                onClick={() => openPageNow(RUTAS_WEB_TRANSPORTISTA.EDITAR, history)}
              >
                <FIcon.Edit size={14} /> Editar
              </button>
            </div>
            <div className="card">
              <div className="card-body">
                <h3 className={'mb-3'}>Mis Datos</h3>
                <div className={'row'}>
                  <AdminItemDetail
                    titulo={'Imagen de perfil'}
                    imagen={Transportista.usuario.imagen_perfil}
                    columnas={3}
                  />
                  <AdminItemDetail titulo={'Nombre'} texto={Transportista.usuario.first_name} columnas={3} />
                  <AdminItemDetail titulo={'Apellido'} texto={Transportista.usuario.last_name} columnas={3} />
                  <AdminItemDetail titulo={'Email'} texto={Transportista.usuario.email} columnas={3} />
                  <AdminItemDetail
                    titulo={'Número de documento'}
                    texto={Transportista.usuario.nro_documento}
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Número de celular'}
                    texto={nroTelefonoLocal(Transportista.usuario.nro_telefono)}
                    columnas={3}
                  />
                  <AdminItemDetail titulo={'Razón social'} texto={Transportista.usuario.razon_social} columnas={3} />
                  <AdminItemDetail titulo={'RUC'} texto={Transportista.usuario.ruc} columnas={3} />
                  <AdminItemDetail
                    titulo={'Ciudades en las que ofrece transporte'}
                    texto={Transportista.ciudades.map((ciu: any) => ciu.nombre).join(', ')}
                    columnas={6}
                  />
                  <AdminItemDetail
                      titulo={'Tipo de Driver'}
                      texto={Transportista.tipo}
                      columnas={6}
                  />
                </div>
                {Transportista.direccion && <>
                  <h3 className={'mb-3 mt-5'}>Dirección</h3>
                  <div className={'row'}>
                    <AdminItemDetail
                      titulo={'Calle principal'}
                      texto={Transportista.direccion.calle_principal}
                      columnas={3}
                    />
                    <AdminItemDetail
                      titulo={'Calle secundaria'}
                      texto={Transportista.direccion.calle_secundaria}
                      columnas={3}
                    />
                    <AdminItemDetail
                      titulo={'Número'}
                      texto={Transportista.direccion.numero}
                      columnas={3}
                    />
                    <AdminItemDetail
                      titulo={'Referencias'}
                      texto={Transportista.direccion.referencia}
                      columnas={3}
                    />
                  </div>
                </>}
                <h3 className={'mb-3 mt-5'}>Mis documentos de conductor</h3>
                <div className={'row'}>
                  <AdminItemDetail
                    titulo={'Licencia de conducir (frente)'}
                    imagen={Transportista.registro_frente}
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Licencia de conducir (dorso)'}
                    imagen={Transportista.registro_dorso}
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Antecedentes judiciales'}
                    imagen={Transportista.antecedentesj}
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Antecedentes policiales'}
                    imagen={Transportista.antecedentesp}
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Fecha de vencimiento de la licencia'}
                    texto={moment(Transportista.registro_vencimiento).format('DD/MM/YYYY')}
                    columnas={3}
                  />
                </div>
                <h3 className={'mb-3 mt-5'}>Contrato de servicios</h3>
                <div className={'row'}>
                  {Transportista.fecha_aceptacion_contrato && (
                    <div className={'col-12 mb-2'}>
                      Contrato aceptado el: {moment(Transportista.fecha_aceptacion_contrato).format('DD/MM/YYYY')}
                    </div>
                  )}
                  <div className={'col-3'}>
                    <button
                      className={'btn btn-primary'}
                      title={'Descargar contrato persona física'}
                      onClick={() => {
                        if (Transportista.usuario.ruc && Transportista.usuario.razon_social) {
                          obtenerContrato('fisica')
                        } else {
                          alert('Cargue sus datos de facturación para continuar')
                        }
                      }}
                    >
                      Descargar para persona física
                    </button>
                  </div>
                  <div className={'col-3'}>
                    <button
                      className={'btn btn-primary'}
                      title={'Descargar contrato persona jurídica'}
                      onClick={() =>{
                        if (Transportista.usuario.ruc && Transportista.usuario.razon_social) {
                          obtenerContrato('juridica')
                        } else {
                          alert('Cargue sus datos de facturación para continuar')
                        }
                      }}
                    >
                      Descargar para persona jurídica
                    </button>
                  </div>
                  <div className={'col-3'}>
                    <button
                      className={'btn btn-primary'}
                      title={'Descargar anexo del contrato'}
                      onClick={() => obtenerContrato('anexo')}
                    >
                      Descargar anexos
                    </button>
                  </div>
                </div>
                <div className={'row mt-3'}>
                  <div className={'col-3'}>
                    {Transportista.fecha_aceptacion_contrato === null && (
                      <button
                        className={'btn btn-primary'}
                        title={'Aceptar contrato'}
                        onClick={() => setAlertContrato(true)}
                      >
                        Aceptar contrato
                      </button>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </Page>
  )
})

export default AdminTransportistaPerfil
